html {
  font-size: 62.5%;
}

* {
  background-color: #121212;
}

.App {
  text-align: center;
  background-color: #121212;
  width: 100%;
  height: 100%;
  /* display: flex; */
  color: white;

}

body {
  padding: 0%;
  margin: 0;
  
}


nav {
  height: 4.2rem;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid grey;
  display: grid;
  place-items: center;
}

nav h1 {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  color: white;
  font-size: 3.5rem;
}
.game {
  width: 100%;
  height: calc(100% - 37rem);
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
  flex-direction: column;
  
}

.board {
  width: 34rem;
  height: 38rem;
  border: 0.1rem solid black;
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
  margin: .4rem;
}

.letter {
  flex: 33%;
  height: 100%;
  border: .1rem solid grey;
  margin: .3rem;
  display: grid;
  place-items: center;
  font-size: 3rem;
  font-weight: bolder;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

#correct {
  background-color: #528d4e;
}

#almost {
  background-color: #b49f39;
}

#error {
  background-color: #3a393c;
}

.keyboard {
  width: 70rem;
  height: 30rem;
  margin-top: 0.7rem;
}

.line1 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: .2rem;
}
.line2 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: .2rem;
}
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: .2rem;
}

.key {
  width: 4rem;
  height: 5.8rem;
  margin: .4rem;
  border-radius: .4rem;
  display: grid;
  place-items: center;
  font-size: 2rem;
  background-color: grey;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}

#big {
  width: 8rem;
}
#disabled {
  background-color: #3a393c;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.7);
}

.modal-content {
  background-color: rgb(18, 18, 19);
  margin: 5% auto;
  padding: 20px;
  width: 80%;
  color: gainsboro;
  max-width: 500px;
}

.modal-content hr {
  border: 1px solid rgb(58, 58, 60);
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
